<template>
  <div id="app" class="container">
    <div class="row" v-if="isLogin">
        <div class="col-xs-12">
          <nav class="navbar navbar-inverse navbar-embossed" role="navigation">
            <div class="navbar-header">
              <button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#navbar-collapse-01">
                <span class="sr-only">Toggle navigation</span>
              </button>
              <a class="navbar-brand" href="#">DayReport</a>
            </div>
            <div class="collapse navbar-collapse" id="navbar-collapse-01">
              <ul class="nav navbar-nav navbar-left">
                <li><router-link :to="{ name: 'index'}">日报列表</router-link></li>
                <li><router-link :to="{ name: 'add'}">新增日报</router-link></li>
              </ul>
              <ul class="nav navbar-nav navbar-right">
                <li><a>欢迎，{{loginUser}}</a></li>
                <li><a @click="logout()">退出登录</a></li>
              </ul>
            </div><!-- /.navbar-collapse -->
          </nav><!-- /navbar -->
        </div>
      </div> <!-- /row -->
    <router-view class="view"></router-view>
  </div>
</template>

<script>

function removeSessionStorage(key){
  window.sessionStorage.removeItem(key);
}

import Base from './views/report/base.js';
export default {
  name: 'app',
  data () {
    return {
      isLogin: null,
      loginUser: window.sessionStorage['report_username']
    }
  },
  mixins: [Base],
  mounted(){
    this.isLogin = this.token?true:false;
    this.$bus.on('loginTypeChange', this.loginTypeChange);
    this.$bus.on('loginNameChange', this.loginNameChange);
  },
  methods: {
    logout(){
      removeSessionStorage('report_token');
      this.$router.push({name: 'login'});
      this.isLogin = false;
    },
    loginTypeChange(value){
      this.isLogin = value;
    },
    loginNameChange(){
      this.loginUser = window.sessionStorage['report_username'];
    }
  }
}
</script>

<style lang="less">
@import "./assets/bootstrap/css/bootstrap.min.css";
@import "./assets/less/flat-ui.less";
.container{
  width: 970px !important;
  position: static!important;
  overflow: auto!important;
}
</style>



