import Vue from 'vue'
import Router from 'vue-router'

const LoginTpl = resolve => {
  require.ensure(['../views/login.vue'], () => {
    resolve(require('../views/login.vue'))
  })
}

const IndexTpl = resolve => {
  require.ensure(['../views/report/index.vue'], () => {
    resolve(require('../views/report/index.vue'))
  })
}

const EditTpl = resolve => {
  require.ensure(['../views/report/edit.vue'], () => {
    resolve(require('../views/report/edit.vue'))
  })
}

const DetailTpl = resolve => {
  require.ensure(['../views/report/detail.vue'], () => {
    resolve(require('../views/report/detail.vue'))
  })
}

Vue.use(Router)

// 创建一个路由器实例
// 并且配置路由规则
const router = new Router({
  mode: 'hash',
  // base: __dirname,
  // linkActiveClass: 'active-link',//如果传参为中文，则没办法自动激活
  routes: [{
    path: '/login',
    component: LoginTpl,
    name: 'login',
    meta: {
      requireAuth: 'required'
    }
  }, {
    path: '/index',
    component: IndexTpl,
    name: 'index'
  }, {
    path: '/add',
    component: EditTpl,
    name: 'add'
  }, {
    path: '/edit/:id',
    component: EditTpl,
    name: 'edit'
  }, {
    path: '/detail/:id',
    component: DetailTpl,
    name: 'detail'
  }, {
    path: '/',
    redirect: '/index',
    name: 'default'
  }]
});

export default router;


